.recipe{
    border-radius: 10px;
    box-shadow: 3px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: white;
    align-items: center;
    min-width: 40%;
    padding: 30px;
}

.image{
    border-radius: 50%;
    width: 100px;
    height: 100px;
}
.link{
    text-decoration: none;
    color: black;
}