.App {
  min-height: 100vh;
  background-image: linear-gradient(-225deg, #2CD8D5 0%, #C5C1FF 56%, #FFBAC3 100%);
}
.recipe img{
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.recipe{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.search-bar{
  border-radius: 10px;
  border: solid 1px violet;
  padding: 10px;
  margin: 30px;
  width: 50%;
  justify-content: center;
  align-items: center;
}
.search-button{
  padding: 10px;
  border: solid 1px greenyellow;
  background-color: hotpink;
  cursor: pointer;
  border-radius: 10px;
}
